<template>
    <div>
        <div class="container-fluid">
            <div class="row">
                <div class="col-md-5 mx-auto">
                    <div class="card chatroom shadow">
                        <div class="card-header">
                            <span style="color:green" v-if="isConnected">You're connected to the server!</span>
                            <span style="color:red" v-if="!isConnected">You're not connected to the server!</span>
                            <span style="color:orange" v-if="reconnect">Reconnecting...</span>
                        </div>
                        <div class="card-body" ref="scroll">
                            <ul v-if="socketMessage.length !== 0">
                                <li v-for="(pesan, index) in socketMessage" :key="index">
                                    <span class="pull-left" v-if="index % 2 === 0" v-html="pesan"></span>
                                    <span class="pull-right" v-if="index % 2 !== 0" v-html="pesan"></span>
                                </li>    
                            </ul>
                        </div>
                        <div class="card-footer">
                            <div class="input-group">
                                <input type="text" autofocus @keyup.enter="check" :disabled="!isConnected" class="form-control" placeholder="Mau tanya apa?" 
                                aria-label="Mau tanya apa?" v-model="pesan">
                                <div class="input-group-append">
                                    <button class="btn btn-primary" @click="check" :disabled="!isConnected" type="button">Kirim</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="waves">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#0099ff" fill-opacity="1" d="M0,64L11.4,64C22.9,64,46,64,69,80C91.4,96,114,128,137,144C160,160,183,160,206,138.7C228.6,117,251,75,274,101.3C297.1,128,320,224,343,250.7C365.7,277,389,235,411,186.7C434.3,139,457,85,480,58.7C502.9,32,526,32,549,42.7C571.4,53,594,75,617,101.3C640,128,663,160,686,170.7C708.6,181,731,171,754,154.7C777.1,139,800,117,823,106.7C845.7,96,869,96,891,128C914.3,160,937,224,960,245.3C982.9,267,1006,245,1029,202.7C1051.4,160,1074,96,1097,101.3C1120,107,1143,181,1166,186.7C1188.6,192,1211,128,1234,138.7C1257.1,149,1280,235,1303,240C1325.7,245,1349,171,1371,144C1394.3,117,1417,139,1429,149.3L1440,160L1440,320L1428.6,320C1417.1,320,1394,320,1371,320C1348.6,320,1326,320,1303,320C1280,320,1257,320,1234,320C1211.4,320,1189,320,1166,320C1142.9,320,1120,320,1097,320C1074.3,320,1051,320,1029,320C1005.7,320,983,320,960,320C937.1,320,914,320,891,320C868.6,320,846,320,823,320C800,320,777,320,754,320C731.4,320,709,320,686,320C662.9,320,640,320,617,320C594.3,320,571,320,549,320C525.7,320,503,320,480,320C457.1,320,434,320,411,320C388.6,320,366,320,343,320C320,320,297,320,274,320C251.4,320,229,320,206,320C182.9,320,160,320,137,320C114.3,320,91,320,69,320C45.7,320,23,320,11,320L0,320Z"></path></svg>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isConnected: false,
            reconnect: false,
            socketMessage: [],
            pesan: '',
            welcome: '',
        }
    },
    sockets:{
        connect() {
            this.isConnected = true;
            sessionStorage.setItem("token", this.$socket.id)
            this.reconnect = false
        },
        reconnect() {
            this.reconnect = true
            this.isConnected = false;
        },
        disconnect() {
            this.reconnect = false
            this.isConnected = false;
        },
        sendMessage(data) {
            this.socketMessage.push(data)
        }
    },
    methods: {
        check() {
            if(this.pesan !== '') {
                this.$socket.emit('sendMessage', {
                    token: sessionStorage.getItem("token"),
                    pesan: this.pesan
                })
                this.pesan = ''
            }
        },
        scrollToEnd () {
            var content = this.$refs.scroll;
            content.scrollTop = content.scrollHeight;
        }
    },
    created() {
        fetch(`https://ipinfo.io/?token=4a97ed6793d20b`)
        .then(response => response.json())
        .then(data => {
            console.log(data)
            localStorage.setItem("ip", data.ip)
            localStorage.setItem("city", data.city)
        });
        let pesan = `Selamat datang. IP Public kamu <b>${localStorage.getItem('ip')}</b> di daerah <b>${localStorage.getItem("city")}</b>. 
                    Saya dengan Indira. Ada yang bisa dibantu?`
        this.socketMessage.push(pesan)
    },
    updated () {
        this.scrollToEnd()
    }
}
</script>